import styled from "styled-components";

export const SliderWrapper=styled.div`
    position: relative;
    max-width: 1020px;
    width: 100%;
    margin: 0 auto;
    
    .slider_content{
        margin: 0 auto;
        position: relative;
        width: 100%;
        max-width: 816px;
        overflow: hidden;
        padding-top: 50px;
        
        .slider_item{
            position: relative;
            width: 100%;
            background: rgba(0, 0, 0, 0.05);
            padding: 80px 125px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            row-gap: 10px;
            
            .slide_item_image_container{
                position: absolute;
                top: 0;
                left: 50%;
                transform: translate(-50%,-50%);
            }
            
            .slide_item_text{
                text-align: center;
            }
        }
        
    }

    .slider_control {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        font-size: 50px;
        color: ${props=>props.theme.colors.secondaryText};
        transition: color .3s;
        &:hover{
            color: ${props=>props.theme.colors.primaryText};
            cursor:pointer;
        }
        
    }
    .slider_control.control_left{
        left: 0;
    }
    .slider_control.control_right{
        right: 0;
    }

`